.App {
  font-family: "Lato", sans-serif;
  text-align: center;
}

.btn {
  font-family: "Roboto", sans-serif;
  cursor: pointer;
  margin: 10px 10px 10px 0;
  padding: 10px 15px;
  background-color: lightblue;
  border: 1px solid lightslategrey;
  border-radius: 5px;
  border: none;
  text-transform: uppercase;
}

.btn:hover {
  filter: brightness(90%);
}

.btn-secondary {
  background-color: lightgray;
}

.color {
  margin: 20px auto;
  text-align: center;
  height: 200px;
  width: 200px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 2px 2px 5px gray;
  cursor: pointer;
}

.yellow {
  background-color: #ffdd00;
}

.orange {
  background-color: #fc8503;
}

.green {
  background-color: #39b54a;
}

.pink {
  background-color: #ef92a2;
}

.blue {
  background-color: #0066b3;
}

.purple {
  background-color: #8760bf;
}

.color.black,
.color.blue {
  color: white;
}

.red {
  background-color: #e51c00;
}

.black {
  background-color: black;
}

.colorLabel {
  text-transform: capitalize;
}

.preview {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 0 5px;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0 auto;
  text-align: left;
  display: table;
}

li {
  padding: 5px 0;
}

.click {
  filter: brightness(80%);
}
